.App {
  margin: 0 20pt;
  color: rgb(96, 75, 60);
}
h1 {
  color: rgb(0, 160, 209);
  font-size: 16pt;
  font-weight: 400;
  & + p {
    font-size: 13pt;
    color: rgb(151, 134, 121);
  }
}

.tabs {
  width: 100%;
  min-height: 40pt;
  position: relative;
  border-radius: 360px;
  margin: 0 auto;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.1);
  color: rgb(96, 75, 60);
  button {
    font-size: 12pt;
    padding: 13pt 0;
    border: none;
    width: 50%;
    position: relative;
    z-index: 1;
    background: none;
    transition: color 0.2s ease-in 0.1s;
    &:focus {outline:0;}
    &.active {
      color: white;
    }
  }

  &:after {
    content: '';
    position: absolute;
    width: 48%;
    margin: 0 1%;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    height: 80%;
    background: #00a0d1;
    border-radius: 360px;
    transition: left 0.3s ease-in-out;
  }
}
.cards {
  margin-top: 20pt;
  .card {
    position: relative;
    display: grid;
    grid-template-columns: 1fr 2fr;
    margin-top:13pt;
    padding-bottom: 17pt; 
    &:before {
      content: '';
      position: absolute;
      height: 1px;
      width: 100%;
      background: rgb(224, 213, 206);
      bottom: 0;
    }
    p {
      font-size: 13pt;
    }
    .image {
      position: relative;
      overflow: hidden;
      height: 0;
      padding-bottom: 100%;
      img {
        position: absolute;
        height: 100%;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }
}

$start: 0;
$end: 1;

@for $i from $start through $end {
  .current-tab--#{$i} {
    &:after {
      left : #{$i * 100% / ($end + 1)};
    }
  }
}
